import AsyncComponent from '../AsyncComponent';

import HomeIcon from '../../assets/images/home.svg';
import QuestionIcon from '../../assets/images/question.svg';
import InfoIcon from '../../assets/images/info.svg';

const HomePage = AsyncComponent(() => import('../Home'));
const Question = AsyncComponent(() => import('../Pages/Question/index'));
const Contact = AsyncComponent(() => import('../Pages/Contact/index'));

const Routes = [
    {
        title: 'Trang chủ',
        path: '/',
        exact: true,
        component: HomePage,
        icon: HomeIcon
    },
    {
        title: 'Hướng dẫn',
        path: '/question',
        exact: false,
        component: Question,
        icon: QuestionIcon
    },
    {
        title: 'Liên hệ',
        path: '/contact',
        exact: false,
        component: Contact,
        icon: InfoIcon
    }
];

export default Routes;
