import React, { PureComponent } from 'react';
import ReactDOM from 'react-dom';
import { Helmet } from 'react-helmet';
import { Router, Route, Switch } from 'react-router-dom';
import PropTypes from 'prop-types';
import FontFaceObserver from 'fontfaceobserver';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Loadable from 'react-loadable';

import { fb_app_id } from '../configs';
import ogImg from '../assets/images/og-img.jpg';

import Routes from './routes';
import PageRoutes from '../containers/commons/routes';

// Add CSS
import '../styles/index.scss';

// Custom
import { muiDefault as muiTheme } from '../configs/material-ui-theme';

// Import Language Provider
import LanguageProvider from '../containers/LanguageProvider';

// Import i18n messages
import { translationMessages } from '../i18n';

const Header = Loadable({
    loader: () => import('../containers/commons/Header'),
    loading: () => null,
    modules: ['header']
});

const Footer = Loadable({
    loader: () => import('../containers/commons/Footer'),
    loading: () => null,
    modules: ['footer']
});

const NotFound = Loadable({
    loader: () => import('../containers/commons/NotFound'),
    loading: () => null,
    modules: ['not-found']
});

class App extends PureComponent {
    componentDidMount() {
        // Observe loading of Open Sans (to remove open sans, remove the <link> tag in
        // the index.html file and this observer)
        const openSansObserver = new FontFaceObserver('Open Sans', {});

        // When Open Sans is loaded, add a font-family using Open Sans to the body
        openSansObserver.load().then(() => {
            document.body.classList.add('fontLoaded');
        });
    }

    render() {
        const { history } = this.props;
        const render = messages => {
            return (
                <LanguageProvider messages={messages}>
                    <MuiThemeProvider theme={muiTheme}>
                        <Router history={history}>
                            <div className="main-container hth-project">
                                <Helmet
                                    titleTemplate="%s - www.xitashop.com"
                                    defaultTitle="www.xitashop.com | The best choice"
                                >
                                    <meta
                                        name="description"
                                        content="Chuyên hàng nhập khẩu chính hãng chất lượng cao, giá tốt nhất"
                                    />
                                    <meta
                                        property="og:url"
                                        content={window.location.href}
                                    />
                                    <meta
                                        property="og:type"
                                        content="website"
                                    />
                                    <meta
                                        property="og:title"
                                        content="www.xitashop.com | The best choice"
                                    />
                                    <meta property="og:image" content={ogImg} />
                                    <meta
                                        property="og:description"
                                        content="Chuyên hàng nhập khẩu chính hãng chất lượng cao, giá tốt nhất"
                                    />
                                    <meta
                                        property="fb:app_id"
                                        content={fb_app_id}
                                    />
                                </Helmet>
                                <Header />
                                <main className="main-content">
                                    <div className="container">
                                        <Switch>
                                            {Routes.length &&
                                                Routes.map((route, key) => (
                                                    <Route
                                                        key={key}
                                                        {...route}
                                                    />
                                                ))}
                                            {PageRoutes.length &&
                                                PageRoutes.map((route, key) => (
                                                    <Route
                                                        key={key}
                                                        {...route}
                                                    />
                                                ))}
                                            <Route component={NotFound} />
                                        </Switch>
                                    </div>
                                </main>
                                <Footer />
                            </div>
                        </Router>
                    </MuiThemeProvider>
                </LanguageProvider>
            );
        };

        if (module.hot) {
            // Hot reloadable React components and translation json files
            // modules.hot.accept does not accept dynamic dependencies,
            // have to be constants at compile-time
            module.hot.accept(['../i18n', this], () => {
                ReactDOM.unmountComponentAtNode(
                    document.getElementById('lang')
                );
                return render(translationMessages);
            });
        }

        return render(translationMessages);
    }
}

App.propTypes = {
    history: PropTypes.object.isRequired
};

export default App;
