import Loadable from 'react-loadable';

const Routes = [
    {
        title: 'Trang chủ',
        path: '/',
        exact: true,
        component: Loadable({
            loader: () => import('../containers/Home'),
            loading: () => null,
            modules: ['SamHanQuoc']
        })
    },
    {
        title: 'Sâm Hàn Quốc',
        path: '/sam-han-quoc/',
        exact: false,
        component: Loadable({
            loader: () => import('../containers/Categories/Labels/SamHanQuoc'),
            loading: () => null,
            modules: ['SamHanQuoc']
        })
    },
    {
        title: 'Nước Hoa',
        path: '/nuoc-hoa/',
        exact: false,
        component: Loadable({
            loader: () => import('../containers/Categories/Labels/NuocHoa'),
            loading: () => null,
            modules: ['NuocHoa']
        })
    },
    {
        title: 'Sản phẩm khác',
        path: '/san-pham-khac/',
        exact: false,
        component: Loadable({
            loader: () => import('../containers/Categories/Labels/SanPhamKhac'),
            loading: () => null,
            modules: ['SanPhamKhac']
        })
    }
];

export default Routes;
